import isEmpty from "./isEmpty"


const validateGiftCardCreation = (giftCardData) => {


    const errors = {}
    if (isEmpty(giftCardData.name)) {
        errors.name = 'Name of tax cannot be empty!'
    }
    if (isEmpty(giftCardData.type)) {
        errors.type = 'Provide discount type of gift card!'
    }
    if (isEmpty(giftCardData.rate)) {
        errors.rate = 'Provide rate of gift card!'
    }
    if (isEmpty(giftCardData.code)) {
        errors.code = 'Provide code of gift card!'
    }
    if (giftCardData.isLimited && isEmpty(giftCardData.countLimit)) {
        errors.countLimit = 'Provide count limit of gift card!'
    }
    //Will be valid if the errors are empty
    const isValid = isEmpty(errors);
    return { isValid, errors }
}




const validateGiftCardCategoryCreation = (giftCardCategoryData) => {

    const errors = {}

    if (isEmpty(giftCardCategoryData.name)) {
        errors.name = 'Name of category cannot be empty!'
    }

    //Will be valid if the errors are empty
    const isValid = isEmpty(errors);
    return { isValid, errors }
}

export { validateGiftCardCreation,validateGiftCardCategoryCreation}