import React from 'react';
import { Row, Col, Card, Form, Button, InputGroup, FormControl, DropdownButton, Dropdown } from 'react-bootstrap';

import Aux from "../../hoc/_Aux";
import Loader from "../../App/components/Reusables/Loader"


import ErrorBody from '../../App/components/Reusables/ErrorBody';
import Jhilikka from '../../App/components/Reusables/Jhilikka';
import { validateGiftCardCreation } from '../../validation/giftCard';
import { createGiftCard, getGiftCard, getGiftCardCategories } from '../../api/payment/giftCards';

const Create = (props) => {

    const [loading, setLoading] = React.useState(true)
    const [submitting, setSubmitting] = React.useState(false)
    const [giftCardCategories, setGiftCardCategories] = React.useState([]);
    const [giftCardData, setGiftCardData] = React.useState({
        name: '',
        category: '',
        type: '',
        rate: '',
        code: '',
        hasUniqueCode: false,
        isLimited: false,
        countLimit: '',
    })
    const [errors, setErrors] = React.useState({});

    React.useEffect(() => {
        const controller = new AbortController();
        getGiftCardCategories({ action: 'get', filter: 'none', start: 0, end: 999999 }, controller.signal)
            .then((res) => {
                console.log(res)
                if (res.json.status) {
                    setGiftCardCategories([...res.json.gift_card_categories]);
                    setLoading(false)
                }
            }).catch((err) => {
                console.log(err)
            });
        return () => controller.abort()
    }, [])

    const amendErrorOf = (field) => {
        setErrors({ ...errors, [field]: '' })
    }



    const _giftCardDataChange = (e) => {
        console.log('chainge hte ghints', e.target.value);
        amendErrorOf(e.target.name)
        const tempGiftCardData = giftCardData;
        // console.log(giftCardData.hasUniqueCode)
        if (e.target.type === 'checkbox') {
            console.log('htis is checkobs ------------------');
            tempGiftCardData[e.target.name] = !tempGiftCardData[e.target.name];
            if (tempGiftCardData.hasUniqueCode === true) {
                tempGiftCardData.isLimited = true
            }
        } else {
            console.log('htis is not not ------------------');
            tempGiftCardData[e.target.name] = e.target.value;
        }

        setGiftCardData({ ...tempGiftCardData });

    }

    console.log(giftCardData);

    const _submit = () => {

        const { isValid, errors } = validateGiftCardCreation(giftCardData);

        if (!isValid) {
            console.log(errors);
            setErrors({ ...errors })
            return;
        };

        console.log('no errros');
        setSubmitting(true)

        //if no errros api call
        const requestJson = {
            "action": "add",
            "name": giftCardData.name,
            "code": giftCardData.code,
            "discount_type": giftCardData.type,
            "rate": parseFloat(giftCardData.rate),
            "is_limited": giftCardData.isLimited,
            "has_unique_codes": giftCardData.hasUniqueCode,
            "count_limit": parseInt(giftCardData.countLimit),
            "category": parseInt(giftCardData.category)
        }
        console.log(JSON.stringify(requestJson));
        createGiftCard(requestJson)
            .then((res) => {
                if (res.json.status) {
                    Jhilikka.fire({ icon: 'success', title: 'Gift Card created successfully!', background: '#dffff0' })
                    props.history.push('/gift-cards');
                } else {
                    setSubmitting(false)
                    Jhilikka.fire({ icon: 'error', title: res.json.error, background: '#ffe5de' })
                }
            }).catch((err) => {
                setSubmitting(false);
                console.log(err)
            });
    }


    if (loading) return <Loader />
    return (
        <Aux>
            <Row>
                <Col>
                    <Card>
                        <Card.Body>
                            <h5>Create Gift Card</h5>
                            <hr />
                            <Row>
                                <Col md={6}>
                                    <Form.Group  >
                                        <Form.Label>Gift Card Name*</Form.Label>
                                        <Form.Control name="name" value={giftCardData.name} onChange={_giftCardDataChange} type="text" placeholder="Gift Card Name" />
                                        <ErrorBody>{errors.name}</ErrorBody>
                                    </Form.Group>
                                </Col>
                                <Col md={6}>

                                    <Form.Group  >
                                        <Form.Label>Gift Card Category</Form.Label>
                                        <Form.Control name="category" value={giftCardData.category} onChange={_giftCardDataChange} as="select">

                                            <option value='' disabled >Select...</option>
                                            {giftCardCategories.map((cat, index) => {
                                                return <option key={index} value={cat.id}  >{cat.name}</option>
                                            })}

                                        </Form.Control>
                                        <ErrorBody>{errors.category}</ErrorBody>
                                    </Form.Group>
                                </Col>
                                <Col md={6}>

                                    <Form.Group  >
                                        <Form.Label>Discount Type*</Form.Label>
                                        <Form.Control name="type" value={giftCardData.type} onChange={_giftCardDataChange} as="select">

                                            <option value='' disabled >Select...</option>
                                            <option value='percent'  >Percent</option>
                                            <option value='fixed'  >Fixed</option>
                                        </Form.Control>
                                        <ErrorBody>{errors.type}</ErrorBody>
                                    </Form.Group>
                                </Col>

                                <Col md={6}>
                                    <Form.Group  >
                                        <Form.Label>Rate*</Form.Label>
                                        <Form.Control name="rate" value={giftCardData.rate} onChange={_giftCardDataChange} type="number" placeholder="Rate" />
                                        <ErrorBody>{errors.rate}</ErrorBody>
                                    </Form.Group>
                                </Col>


                                <Col md={6}>
                                    <Form.Group  >
                                        <Form.Label>Code*</Form.Label>
                                        <Form.Control name="code" value={giftCardData.code} onChange={_giftCardDataChange} type="text" placeholder="Code" />
                                        <ErrorBody>{errors.code}</ErrorBody>
                                    </Form.Group>
                                </Col>
                                <Col md={3}>
                                    <Form.Group  >
                                        <Form.Label></Form.Label>
                                        <Form.Check name="hasUniqueCode" onChange={_giftCardDataChange} checked={giftCardData.hasUniqueCode} style={{ padding: '5px', paddingLeft: '20px' }} type="checkbox" label="Has Unique Code" />
                                        <ErrorBody>{errors.hasUniqueCode}</ErrorBody>
                                    </Form.Group>
                                </Col>

                                <Col md={3}>
                                    <Form.Group  >
                                        <Form.Label></Form.Label>
                                        <Form.Check name="isLimited" onChange={_giftCardDataChange} checked={giftCardData.isLimited} style={{ padding: '5px', paddingLeft: '20px' }} type="checkbox" label="Is Limited" />
                                        <ErrorBody>{errors.isLimited}</ErrorBody>
                                    </Form.Group>
                                </Col>

                                {giftCardData.isLimited ? (
                                    <Col md={6}>
                                        <Form.Group  >
                                            <Form.Label>Count Limit*</Form.Label>
                                            <Form.Control name="countLimit" value={giftCardData.countLimit} onChange={_giftCardDataChange} type="number" placeholder="Count Limit" />
                                            <ErrorBody>{errors.countLimit}</ErrorBody>
                                        </Form.Group>
                                    </Col>
                                ) : null}



                            </Row>
                            <div className="d-flex justify-content-end" >
                                <Button
                                    variant="warning"
                                    onClick={() => { props.history.push('/gift-cards') }}
                                >
                                    Cancel
                                    </Button>
                                <Button
                                    disabled={submitting}
                                    variant="primary"
                                    onClick={_submit}
                                >
                                    {submitting ? <><i className="fa fa-spinner text-c-white f-15 fa-pulse m-l-1"></i>Submitting</> : 'Submit'}
                                </Button>
                            </div>

                        </Card.Body>
                    </Card>
                </Col>
            </Row>
        </Aux>
    );
}

export default Create;
